import React from 'react'
import { Link, Outlet, useLocation, useNavigate } from 'react-router-dom'

const Portfolio = () => {
    let navigate=useNavigate()
    let location=useLocation()
    let locationValue=location.pathname
    console.log(locationValue)
    return (
        <>
            <div className="container">
                <div className=" fs-2 heading text-center border border-dark">
                    Portfolio
                </div>
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                    <div className="container d-flex justify-content-center">
                        <ul className="nav nav-tabs ">
                            <li className="nav-item">
                                <Link className={`nav-link active ${locationValue=="/portfolio/all"?"active":""} `} aria-current="page" to="/portfolio/all">All</Link>
                            </li>
                            <li className="nav-item">
                                <Link className={`nav-link ${locationValue=="/portfolio/coded"?"active":""} `} to="/portfolio/coded">Coded</Link>
                            </li>
                            <li className="nav-item">
                                <Link className={`nav-link ${locationValue=="/portfolio/designed"?"active":""} `} to="/portfolio/designed">Designed</Link>
                            </li>
                        </ul>
                    </div>
                </nav>
                <Outlet />
                <footer>
                    <div className="projects-footer fs-4 text-center py-1">And many more to come...</div>
                </footer>
            </div>
        </>
    )
}

export default Portfolio
